import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { CSVLink } from 'react-csv';
import { v4 as uuidv4 } from 'uuid';
import { FaEdit } from 'react-icons/fa';

export const SearchableCustomerTable = ({ data, setOpenInvoiceList }) => {
    const [searchTerm, setSearchTerm] = useState('');
    const [mData, setmData] = useState([]);
    const [filteredData, setFilteredData] = useState([]);
    const [loading, setLoading] = useState(true);
    const [selectedRows, setSelectedRows] = useState([]);

    useEffect(() => {
        if (data.length === 0) {
            setLoading(true);
        } else {
            const dataWithIds = data.map(item => ({ ...item, id: uuidv4() }));
            setmData(dataWithIds)
            setFilteredData(dataWithIds);
            setLoading(false);
        }
    }, [data]);

    const handleSearch = (e) => {
        const term = e.target.value.toLowerCase();
        setSearchTerm(term);
        const filtered = mData.filter(item =>
            Object.values(item).some(val =>
                String(val).toLowerCase().includes(term)
            )
        );
        setFilteredData(filtered);
        setSelectedRows([]); // Reset selected rows
    };

    const handleCheckboxChange = (e, rowId) => {
        if (e.target.checked) {
            setSelectedRows([...selectedRows, rowId]);
        } else {
            setSelectedRows(selectedRows.filter(id => id !== rowId));
        }
    };
    
    const handleSelectAllChange = (e) => { 
        if (e.target.checked) { 
            const allRowIds = filteredData.map(row => row.id); 
            setSelectedRows(allRowIds); 
        } else { 
            setSelectedRows([]); 
        } 
    };

    return (
        <div className="container">
            <div className="card shadow-sm p-3">
                    
            <h6> 
                {"Customer Data Table"}
            </h6>
            <div className="row mb-3">
                <div className="col-md-8">
                    <input
                        type="text"
                        className="form-control"
                        placeholder="Search..."
                        value={searchTerm}
                        onChange={handleSearch}
                    />
                </div>
                <div className="col-md-4 d-flex justify-content-end">
                    <CSVLink
                        data={filteredData.filter(row => selectedRows.includes(row.id)).map(({ id, ...rest }) => { const filteredRow = {}; Object.keys(rest).forEach(key => { if (!key.startsWith('_')) { filteredRow[key] = rest[key]; } }); return filteredRow; })}
                        filename={"exported-data.csv"}
                        className={`btn btn-success ${selectedRows.length === 0 ? 'disabled' : ''}`}
                        target="_blank"
                        onClick={(e) => {
                            if (selectedRows.length === 0) {
                                e.preventDefault();
                            }
                        }}
                    >
                        Export Selected Rows
                    </CSVLink>
                </div>
            </div>
            {loading ? (
                <div className="d-flex justify-content-center">
                    <div className="spinner-border" role="status">
                        <span className="sr-only">.</span>
                    </div>
                </div>
            ) : (
                <div className="table-responsive" style={{ maxHeight: '600px', overflowY: 'auto' }}>
                    <table className="table table-responsive table-bordered">
                        <thead>
                            <tr>
                                <th> 
                                    <input type="checkbox" 
                                        onChange={handleSelectAllChange} 
                                        checked={selectedRows.length === filteredData.length && filteredData.length > 0} 
                                        /> 
                                </th>
                                {Object.keys(data[0]).filter(key => key !== 'id').filter(key => !key.startsWith("_")).map((key) => (
                                    <th key={key}>{key}</th>
                                ))}
                                <th> 
                                    
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {filteredData.map((row, rowIndex) => (
                                <tr key={row.id}>
                                    <td>
                                        <input
                                            type="checkbox"
                                            onChange={(e) => handleCheckboxChange(e, row.id)}
                                            checked={selectedRows.includes(row.id)}
                                        />
                                    </td>
                                    {Object.keys(row).filter(key => key !== 'id').filter(key => !key.startsWith("_")).map((key) => (
                                        <td key={key}>{row[key]}</td>
                                    ))}
                                    <td> 
                                        <button 
                                            className="btn btn-link" 
                                            onClick={() => setOpenInvoiceList(row)} > 
                                            Open Invoice List
                                        </button> 
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            )}
            </div>
        </div>
    );
};




