const CACHE = new Map();

export const SignIn = () => {
    const signInWithGoogle = () => {
        signInWithPopup(auth, provider)
            .then((result) => {
                localStorage.setItem("email",result.user.email);
                localStorage.setItem("displayName",result.user.displayName);
                window.location.reload();
            })
            .catch((error) => {
                console.error('Error signing in:', error);
            });
    };

    return (
        <button onClick={signInWithGoogle}>Sign in with Google</button>
    );
};

const READ_CACHE = (key)=>{
    if(CACHE.get(key)!=undefined) {
        const age = (Date.now() - CACHE.get(key)["timestamp"]) / 1000 / 60; // Age in minutes
        // console.log("Check", age);
        if (age > 10) {
            CACHE.set(key,undefined);
            // console.log("MISS", CACHE, age);
            return undefined;
        }
        // console.log("HIT", CACHE, age);
        return CACHE.get(key)["response"];
    }
    return undefined;
}
const WRITE_CACHE = (key, data)=>{
    CACHE.set(key,{
        "response": data,
        "timestamp": Date.now()
    })
}

export const WRITE_DOCUMENT = async (collectionName, documentName, data)=>{
    try {
        await setDoc(doc(db, collectionName, documentName), data);        
    } catch(e) {
        console.error("Error WRITE_DOCUMENT: ", e);
    }
}


