export const READ_ALL_COMPANY = async (setAllCustomersData)=>{
    const key = "READ_ALL_COMPANY";
    try {
        // const cache_data = READ_CACHE(key);
        // if(cache_data!=undefined) {
        //     setAllCustomersData(cache_data);
        //     return cache_data;
        // }

        const querySnapshot = await getDocs(collection(db, SCHEMA.COMPANY));
        const data = new Map();
        querySnapshot.forEach((doc) => {
            data.set(doc.id, {
                [COMPANY_ID]: doc.id,        
                "Company Name": doc.data().companyName,
                "Address": doc.data().address,
                "GST": doc.data().gst,
                "Phone": doc.data().phone,
                "Email": doc.data().email,
                "Comments": doc.data().comments
            });
            // console.log(doc.id, doc.data())
        });
        setAllCustomersData(data);
        // WRITE_CACHE(key, data);
    } catch (error) {
        console.error('Error reading customer :', error);
        return { error: error.message };
    }
}
export const READ_ALL_ITEM = async (company, setAllItemsData)=>{
    try {
        const company_id = company[COMPANY_ID]
        const querySnapshot = await getDocs(collection(db, SCHEMA.COMPANY, company_id, SCHEMA.ITEM_DETAILS));
        const data = new Map();
        querySnapshot.forEach((doc) => {
            data.set(doc.id, {
                [COMPANY_ID]: company_id,
                [ITEM_ID]: doc.id,        
                "Item Name": doc.data()["Item Name"],
                "HSN/SAC": doc.data()["HSN/SAC"],
                "Rate": doc.data()["Rate"],
                "Inter State Tax Rate": doc.data()["Inter State Tax Rate"],
                "Opening Stock": doc.data()["Opening Stock"],
                "Stock On Hand": doc.data()["Stock On Hand"],
                "Purchase Rate": doc.data()["Purchase Rate"]
            });
        });
        setAllItemsData(data);
        // WRITE_CACHE(key, data);
    } catch (error) {
        console.error('Error reading customer :', error);
        return { error: error.message };
    }
}



