export const READ_COLLECTION_DATA = async (collectionName, setCollectionData)=>{
    try {
        const querySnapshot = await getDocs(collection(db, collectionName));
        const data = [];
        querySnapshot.forEach((doc) => {
            const x = doc.data();
            data.push({
                "Item Name": x["Item Name"],
                "HSN/SAC": x["HSN/SAC"],
                "Rate": x["Rate"],
                "Inter State Tax Rate": x["Inter State Tax Rate"],
                "Opening Stock": x["Opening Stock"],
                "Stock On Hand": 0,
                "Purchase Rate": x["Purchase Rate"],
                "_collectionName": collectionName,
                "_id": doc.id,
            })
        });
        setCollectionData(data);
    } catch(e) {
        console.error("Error READ_COLLECTION: ", e);
    }
}

export const READ_COLLECTION_DATA_ID_LIST = async (collectionName, setCollectionData)=>{
    try {
        const querySnapshot = await getDocs(collection(db, collectionName));
        const data = [];
        querySnapshot.forEach((doc) => {
            data.push({
                "Id": doc.id
            });
        });
        setCollectionData(data);
    } catch(e) {
        console.error("Error READ_COLLECTION: ", e);
    }
}

export const READ_DOCUMENT_DATA = async (collectionName, documentName, setDocumentData)=>{
    try {
        const docSnap = await getDoc(doc(db, collectionName, documentName));
        if (docSnap.exists()) { 
            setDocumentData(docSnap.data()); 
        } else { 
            console.error("Error READ_DOCUMENT: No such document!"); 
        }
    } catch(e) {
        console.error("Error READ_DOCUMENT: ", e);
    }
}




