export const addJsonToFirestore = async (a, b, c, d, jsonData) => {
    try {
        
        const messageRef = doc(db, a, b, c, d);
        
        await setDoc(messageRef, jsonData);

        console.log('Document successfully written!');
    } catch (error) {
        console.error('Error writing document:', error);
    }
};
