import { useEffect, useState } from "react";
import { list_all_company, list_all_items } from "../service/reader";
import { TableView } from "../component/TableView";
import { TableViewPopUp } from "../component/TableViewPopUp";
import { SearchableCompanyTable } from "../component/TableCompany";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Box, Button, Modal } from '@mui/material';
import { READ_ALL_COMPANY, READ_ALL_ITEM, READ_CACHE, SCHEMA } from "../service/api";
import { SearchableItemTable } from "../component/TableItem";
const _ = require("lodash"); 

export const Company = ()=>{    
    const [showAddCompanyForm, setshowAddCompanyForm] = useState(false);
    const [showCompanyTable, setShowCompanyTable] = useState(true);
    const [showItemTable, setShowItemTable] = useState(undefined);

    const [allCustomerData, setAllCustomersData] = useState([]);
    const [allItemData, setAllItemsData] = useState([]);
    useEffect(()=>{
        READ_ALL_COMPANY(setAllCustomersData);
    },[])
    useEffect(()=>{
        if(showItemTable!=undefined){
            READ_ALL_ITEM(showItemTable, setAllItemsData);
        }
    },[showItemTable])
    
    const company_details_list = Array.from(allCustomerData.values());
    const item_details_list = Array.from(allItemData.values());
 
    return <>
        {/* {showAddCompanyForm==true && <AddCompanyForm 
            showAddCompanyForm={showAddCompanyForm}
            setshowAddCompanyForm={(x)=>{setshowAddCompanyForm(x)}}/>} */}

        {showCompanyTable==true && <>
            {/* <div className="container d-flex justify-content-end">
                <button className="btn btn-primary btn-sm me-2 my-2"
                    onClick={()=>{
                        setshowAddCompanyForm(true);
                }}> Add new customer </button>
            </div>   */}
            <SearchableCompanyTable 
                data={company_details_list}
                setOpenItem={(company)=>{
                    setShowCompanyTable(false);
                    setShowItemTable(company);
                }}/>
        </>}

        {showItemTable!=undefined && <>            
            <SearchableItemTable
                data={item_details_list}
            />
        </>}
    </>;
}

