import { useEffect, useState } from "react";
import { SearchableCustomerTable } from "../component/TableCustomer";
import { READ_ALL_CUSTOMER, WRITE_NEW_CUSTOMER } from "../service/api";
import { Box, Button, Modal } from '@mui/material';


export const Customer = ()=>{
    const [showCustomerTable, setShowCustomerTable] = useState(true);
    const [showInvoiceTable, setShowInvoiceTable] = useState(undefined);
    const [showAddCustomerForm, setshowAddCustomerForm] = useState(false);

    const [allCustomerData, setAllCustomersData] = useState([]);


    useEffect(()=>{
        READ_ALL_CUSTOMER(setAllCustomersData);
    },[])

    const customer_details_list = Array.from(allCustomerData.values());
    return <>

        {showAddCustomerForm==true && <AddCustomerForm 
            showAddCustomerForm={showAddCustomerForm}
            setshowAddCustomerForm={(x)=>{setshowAddCustomerForm(x)}}/>}
        {showCustomerTable==true && <>
            <div className="container d-flex justify-content-end">
                <button className="btn btn-primary btn-sm me-2 my-2"
                    onClick={()=>{
                        setshowAddCustomerForm(true);
                }}> Add new customer </button>
            </div>  
            <SearchableCustomerTable 
                data={customer_details_list}
                setOpenInvoiceList={(customer)=>{
                    setShowCustomerTable(false);
                    setShowInvoiceTable(customer);
                }}/>
        </>}

        {showInvoiceTable!=undefined && <>  
            {JSON.stringify(showInvoiceTable)}          
            
            <div className="container d-flex justify-content-end">
                <button className="btn btn-primary btn-sm me-2 my-2"
                    onClick={()=>{
                        setshowAddCustomerForm(true);
                }}> Add new invoice </button>
            </div>  

        </>}
    </>
}

const AddCustomerForm = ({showAddCustomerForm, setshowAddCustomerForm})=>{
    const [formData, setFormData] = useState({
        customerName: '',
        address: '',
        gst: '',
        phone: '',
        email: '',
        comments: '',
    });
    
    const [errors, setErrors] = useState({});

    const handleChange = (e) => {
    setFormData({
        ...formData,
        [e.target.name]: e.target.value,
    });
    };

    const handleSubmit = (e) => {
        e.preventDefault();

        // Basic validation
        const newErrors = {};
        if (!formData.customerName) {
            newErrors.customerName = 'Customer name is required';
        }
        if (!formData.address) {
            newErrors.address = 'Address is required';
        }
        if (!formData.gst) {
            newErrors.gst = 'GST number is required';
        }
        if (!formData.phone) {
            newErrors.phone = 'Phone number is required';
        }
        if (!formData.email) {
            newErrors.email = 'Email is required';
        }
        // ... more complex validation rules for email and phone number can be added

        setErrors(newErrors);
        console.log(formData)

        if (Object.keys(newErrors).length === 0) {
            // Handle form submission here, e.g., send data to server
            callSubmitApi(formData);
        }
    };

    const callSubmitApi = (formData) => {
        // alert(JSON.stringify(formData));    
        WRITE_NEW_CUSTOMER(formData.customerName, formData);
    }

    return <>
        <Modal
            aria-labelledby="unstyled-modal-title"
            aria-describedby="unstyled-modal-description"
            open={showAddCustomerForm}
            onClose={()=>{setshowAddCustomerForm(false)}}
            sx={{width: '100%',height: '100%'}}
        >
            <div className="container" style={{backgroundColor: 'white', marginTop: '5px', borderRadius:'2px', padding: '10px'}}>
                <form onSubmit={handleSubmit}>
                <div className="mb-3">
                    <label htmlFor="customerName" className="form-label">Customer Name</label>
                    <input
                    type="text"
                    className={`form-control ${errors.customerName ? 'is-invalid' : ''}`}
                    id="customerName"
                    name="customerName"
                    value={formData.customerName}
                    onChange={handleChange}
                    placeholder="Enter customer name"
                    />
                    {errors.customerName && <div className="invalid-feedback">{errors.customerName}</div>}
                </div>

                <div className="mb-3">
                    <label htmlFor="address" className="form-label">Address</label>
                    <textarea
                    className={`form-control ${errors.address ? 'is-invalid' : ''}`}
                    id="address"
                    name="address"
                    value={formData.address}
                    onChange={handleChange}
                    placeholder="Enter address"
                    />
                    {errors.address && <div className="invalid-feedback">{errors.address}</div>}
                </div>

                <div className="mb-3">
                    <label htmlFor="gst" className="form-label">GST Number</label>
                    <input
                    type="text"
                    className={`form-control ${errors.gst ? 'is-invalid' : ''}`}
                    id="gst"
                    name="gst"
                    value={formData.gst}
                    onChange={handleChange}
                    placeholder="Enter GST number"
                    />
                    {errors.gst && <div className="invalid-feedback">{errors.gst}</div>}
                </div>

                <div className="mb-3">
                    <label htmlFor="phone" className="form-label">Phone Number</label>
                    <input
                    type="tel"
                    className={`form-control ${errors.phone ? 'is-invalid' : ''}`}
                    id="phone"
                    name="phone"
                    value={formData.phone}
                    onChange={handleChange}
                    placeholder="Enter phone number"
                    />
                    {errors.phone && <div className="invalid-feedback">{errors.phone}</div>}
                </div>

                <div className="mb-3">
                    <label htmlFor="email" className="form-label">Email Address</label>
                    <input
                        type="email"
                        className={`form-control ${errors.email ? 'is-invalid' : ''}`}
                        id="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                        placeholder="Enter email address"
                    />
                    {errors.email && <div className="invalid-feedback">{errors.email}</div>}
                </div>

                <div className="mb-3">
                    <label htmlFor="comments" className="form-label">Comments</label>
                    <textarea
                        className="form-control"
                        id="comments"
                        name="comments"
                        value={formData.comments}
                        onChange={handleChange}
                        placeholder="Enter comments"
                    />
                </div>

                <button type="submit" className="btn btn-primary">Submit</button>
                </form>
            </div>
        </Modal>
    </>
}